<template>
  <div>
    <div class="activityContent--section users" v-if="localUsers.length > 0">
      
      <div class="users--people">
        <h5>Participating Users</h5>
        <div class="users--user" v-for="user in localUsers" v-bind:key="user.id">
          <User :user="user" :view-mode="'DETAILED'"/>
        </div>
      </div>

      <div class="users--placeholder" v-if="localPlaceholders && localPlaceholders.length > 0">
        <h5>Placeholders</h5>
        <div class="users--user" v-for="user in localPlaceholders" v-bind:key="user.id">
          <User :user="user" :view-mode="'DETAILED'"/>
        </div>
      </div>
    </div>

    <dropdown :class-name="'dropdown'" ref="adduserdropdown" :before-open="loadContacts">
        <template slot="btn"><i class="icon-add-small"></i></template>
        <template slot="body">
          <div class="users--dropdown" v-for="user in contacts" v-bind:key="user.id">
            <div @click="inviteUser(user)">
              <User :user="user" :view-mode="'DETAILED'"/>
            </div>
          </div>
          <hr/>
          <div>
            <ExpandingCreateControl
              v-bind:toggleButtonText="'Add Placeholder'"
              v-bind:inputPlaceholder="'Placeholder Name'"
              v-bind:createButtonText="'Add'"
              v-on:createTriggered="createPlaceholder"
              />
          </div>
        </template>
      </dropdown>
  </div>
</template>

<script>
import api from "./../api.js";
import _ from "lodash";
import Dropdown from "bp-vuejs-dropdown";

import User from "./User.vue";
import ExpandingCreateControl from "./controls/ExpandingCreateControl.vue";

export default {
  name: "Users",
  props: ["users", "placeholders", "apiURL"],
  components: {
    User,
    Dropdown,
    ExpandingCreateControl
  },
  data: function() {
    return {
      contacts: null,
      localUsers: this.users,
      localPlaceholders: this.placeholders
    };
  },
  methods: {
    loadContacts(resolve) {
      if (this.contacts) {
        resolve();
        return;
      }

      api
        .getAll(`/contacts`)
        .then(result => {
          const missing = _.differenceBy(
            result,
            this.localUsers,
            o => o.user.id
          );
          this.contacts = missing;
          resolve();
        })
        .catch(err => console.log(err));
    },
    inviteUser(user) {
      const update = {
        accessLevel: "MEMBER",
        userId: user.id
      };

      api
        .postOne(`${this.apiURL}/users`, update)
        .then(result => {
          this.localUsers.push(result);
        })
        .catch(err => console.log(err));
    },
    createPlaceholder(name) {
      const update = {
        name: name
      };

      api
        .postOne(`${this.apiURL}/placeholders`, update)
        .then(result => {
          this.localPlaceholders.push(result);
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.users {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
</style>



