<template>
  <div>
    <div class="activityContent--section users" v-if="users.length > 0">
      <div class="users--assigned">
        <h5>Assigned User</h5>
        <div class="users--user" v-for="user in assignedUser" v-bind:key="user.id">
          <User :user="user" :view-mode="'DETAILED'"/>
        </div>
      </div>
      <div class="users--subscribed">
        <h5>Subscribed Users</h5>
        <div class="users--user" v-for="user in subscribedUsers" v-bind:key="user.id">
          <User :user="user" :view-mode="'DETAILED'"/>
        </div>
      </div>
    </div>
    <dropdown :class-name="'dropdown'" :unscroll="'activityDetails'" :before-open="loadWorkflowUsers">
      <template slot="btn"><i class="icon-add-small"></i></template>
      <template slot="body">
        <div v-if="workflowUsers && workflowUsers.length > 0">
          <div class="users--dropdown" v-for="user in workflowUsers" v-bind:key="user.id">
            <div @click="assignUser(user)">
              <User :user="user" :view-mode="'DETAILED'"/>
            </div>
          </div>
        </div>
        <div v-else>
          No one left to assign :(
        </div>
      </template>
    </dropdown>
  </div>
</template>

<script>
import api from "./../api.js";
import _ from "lodash";
import Dropdown from "bp-vuejs-dropdown";

import User from "./User.vue";

export default {
  name: "Users",
  props: ["users", "apiURL", "activityID"],
  components: {
    User,
    Dropdown
  },
  data: function() {
    return {
      assignedUser: _.filter(
        this.users,
        o => o.assignedAction === "EXECUTE"
      ).map(x => x.assignedTo),
      subscribedUsers: _.filter(
        this.users,
        o => o.assignedAction === "SUBSCRIBE"
      ).map(x => x.assignedTo),
      workflowUsers: null
    };
  },
  methods: {
    loadWorkflowUsers(resolve) {
      if (this.workflowUsers) {
        resolve();
        return;
      }

      api
        .getAll(`${this.apiURL}/people`)
        .then(result => {
          console.log(result, this.users);
          const missing = _.differenceBy(
            result,
            this.users.map(o => o.assignedTo),
            o => (o.user ? o.user.id : o.id)
          );
          this.workflowUsers = missing;
          resolve();
        })
        .catch(err => console.log(err));
    },
    assignUser(user) {
      const update = {
        assignedAction: "EXECUTE",
        personId: user.id,
        personType: user.user ? "PERSON_USER" : "PERSON_PLACEHOLDER"
      };

      api
        .postOne(
          `${this.apiURL}/activities/${this.activityID}/assignments`,
          update
        )
        .then(result => {
          console.log(result);
          //this.assignedUser = result;
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.users {
  display: grid;
  grid-template-columns: min-content auto;
  grid-gap: spacing(2);
}

.users--assigned {
  white-space: nowrap;
}

.users--user {
  display: inline-block;
}
</style>



