<template>
    <div class="workflows" v-bind:class="[showWorkflows ? '' : 's-collapsed']">
      <div class="workflows--viewport">
        <div class="workflows--header">
          <h5>Workflows</h5>
        </div>
        <div class="workflows--list" v-if="workflows && workflows.length > 0">        
          <WorkflowTile 
              v-for="workflow in workflows" 
              v-bind:key="workflow.id" 
              v-bind:workflow="workflow"/>
        </div>
        <ExpandingCreateControl
          v-bind:toggleButtonText="'Create Workflow'"
            v-bind:inputPlaceholder="'Workflow Name'"
            v-bind:createButtonText="'Add Workflow'"
            v-on:createTriggered="createWorkflow"
            />
      </div>
    </div>
  
</template>

<script>
import api from "./../api.js";
import store from "./../store.js";

import WorkflowTile from "./WorkflowTile.vue";

import ExpandingCreateControl from "./controls/ExpandingCreateControl.vue";

export default {
  name: "Workflows",
  props: ["apiURL"],
  components: {
    WorkflowTile,
    ExpandingCreateControl
  },
  data: function() {
    return {
      workflows: null
    };
  },
  computed: {
    user() {
      return store.state.user;
    },
    showWorkflows() {
      return store.state.showWorkflows;
    }
  },
  watch: {
    user() {
      this.getFlows();
    }
  },
  mounted() {
    //console.log("mounted", this.workflows ? "has flows" : "no flows");
    if (!this.workflows) this.getFlows();
  },
  methods: {
    getFlows() {
      api
        .getAll("workflows")
        .then(result => {
          this.workflows = result;
        })
        .catch(err => console.log(err));
    },
    createWorkflow(newWorkflowName) {
      const workflow = {
        diagramType: "LISTCHART",
        executionOrder: "POSITION",
        name: newWorkflowName,
        state: "DESIGNING"
      };

      api
        .postOne("workflows", workflow)
        .then(result => {
          console.log(result);
          this.workflows.push(result);
          this.$emit("workflowsUpdated", result);
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.workflows {
  grid-area: sidebar;
  width: 25vw;
  display: grid;
  min-height: 0;
  overflow: hidden;
  transition: width 0.3s ease-in-out;

  &.s-collapsed {
    width: 0;
  }
}

.workflows--viewport {
  width: 25vw;
  padding: spacing(1);
  display: grid;
  grid-template-rows: spacing(4) 1fr;
  grid-template-areas: "header" "body";
  min-height: 0;

  * {
    min-height: 0;
  }
}

.workflows--header {
  grid-area: header;
  box-shadow: $list-header-shadow;
}

.workflows--list {
  grid-area: body;
  overflow-y: auto;
  padding: spacing(1);
  margin-bottom: spacing(1);
  * {
    min-height: 0;
  }
}
</style>




