<template>
    <div class="activity-tile" @click="()=> this.$emit('selectedActivityUpdated',activity.id)">
        <div class="activity-tille--assignee">
          <User :user="assignedUser"/>
        </div>
        <div class="activity-tile--text">
          <h3 class="activity-tile--title">{{ activity.name }}</h3>
          <span class="activity-tile--description" v-if="activity.description">{{ activity.description }}</span>
        </div>
        <div class="activity-tile--state">State: {{ activity.state }}</div>
        <div class="activity-tile--meta">Created By: {{ activity.metadata.userMetadata.modifiedBy.fullName }}</div>
    </div>
</template>

<script>
import User from "./User.vue";

export default {
  name: "ActivityTile",
  props: ["activity", "apiURL", "loadActivity"],
  components: {
    User
  },
  data: function() {
    return {};
  },
  computed: {
    assignedUser() {
      const user = this.activity.assignments.filter(
        x => x.assignedAction === "EXECUTE"
      );
      return user[0] ? user[0].assignedTo : null;
    }
  },
  methods: {}
};
</script>

<style lang="scss">
@import "./../scss/vars";

.activity-tile {
  @include containerBox;
  padding: spacing(2) spacing(3);
  display: grid;
  grid-template-columns: 60px 1fr 1fr 1fr;
  grid-template-areas:
    "avatar header header header"
    "state state meta meta";
  text-align: left;

  &:hover {
    .activity-tile--action {
      visibility: visible;
    }
  }
}
.activity-tille--assignee {
  grid-area: avatar;
}

.activity-tile--text {
  grid-area: header;
}

.activity-tile--title {
  padding: 0;
  display: inline-block;
}

.activity-tile--description {
  display: inline-block;
  margin-left: spacing(2);
  font-style: italic;
}

.activity-tile--state {
  grid-area: state;
  font-weight: 700;
}

.activity-tile--meta {
  grid-area: meta;
}

.activity-tile--action {
  grid-area: header-right;
  visibility: hidden;
}
</style>



