<template>
    <div class="account">
        <h1 class="account--header" v-if="user">{{ user.fullName }} - Account</h1>
        
        <nav class="account--nav">
            <label class="account--nav-item">
                <input type="radio" v-model="activeTab" value="CONTACTS"/>
                <span>Contacts</span>
            </label>
            <label class="account--nav-item">
                <input type="radio" v-model="activeTab" value="NOTIFICATIONS"/>
                <span>Notification Preferences</span>
            </label>
        </nav>

        <section class="account--prefs">

            <div class="prefs--page" v-if="contacts && activeTab === 'CONTACTS'">
                <h2>Contacts</h2>
                <User v-for="user in contacts" :key="user.id" :user="user"/>
            </div>

            <div class="prefs--page" v-if="preferences  && activeTab === 'NOTIFICATIONS'">
                <h2>Notification Preferences</h2>
                <div class="prefs--section">
                  <h4>Email Preferences</h4>
                  <div class="toggle--group">
                    <label class="button__toggle">
                        <input type="radio" v-model="preferences.emailDeliveryType" value="IMMEDIATE"/>
                        <span>Send all</span>
                    </label>
                    <label class="button__toggle">
                        <input type="radio" v-model="preferences.emailDeliveryType" value="BATCH"/>
                        <span>Send in batches</span>
                    </label>
                    <label class="button__toggle">
                        <input type="radio" v-model="preferences.emailDeliveryType" value="NONE"/>
                        <span>Never send</span>
                    </label>
                  </div>
                </div>
                <div class="prefs--section" v-if="preferences.emailDeliveryType === 'BATCH'">
                    <h4>Batch Email Frequency</h4>
                    <input type="range" step="5" min="5" max="120" :value="preferences.emailFrequency" @input="updateFrequency"/>
                    <div>Every {{ preferences.emailFrequency }} Minutes</div>
                </div>
                <div class="prefs--section">
                  <h4>@Mention Notification Frequency</h4>
                  <input type="range" step="5" min="5" max="120" :value="preferences.emailMentionsFrequency" @input="updateMentionsFrequency"/>
                  <div>Every {{ preferences.emailMentionsFrequency }} Minutes</div>
                </div>
                
                <button @click="updatedPreferences" :disabled="saving">Save</button>
                <span v-if="saved">*Changes Saved</span>
            </div>

        </section>

    </div>
</template>

<script>
import api from "./../../api.js";
import store from "./../../store.js";

import User from "./../User.vue";

export default {
  name: "Organization",
  components: {
    User
  },
  data() {
    return {
      preferences: null,
      contacts: null,

      saving: false,
      saved: false,

      activeTab: "CONTACTS"
    };
  },
  computed: {
    user() {
      return store.state.user;
    }
  },
  watch: {
    user: function(newVal) {
      if (!newVal) return;
      this.getContacts();
      this.getNotificationPreferences();
    }
  },
  created() {
    if (!this.user) {
      console.log("no user");
      return;
    }
    this.getContacts();
    this.getNotificationPreferences();
  },
  methods: {
    updateFrequency(e) {
      this.preferences.emailFrequency = parseInt(e.currentTarget.value);
    },
    updateMentionsFrequency(e) {
      this.preferences.emailMentionsFrequency = parseInt(e.currentTarget.value);
    },
    getContacts() {
      api
        .getAll(`/contacts`)
        .then(result => {
          this.contacts = result;
        })
        .catch(err => console.log(err));
    },
    getNotificationPreferences() {
      api
        .getAll(`/users/${this.user.id}/notification-settings`)
        .then(result => {
          this.preferences = result;
        })
        .catch(err => console.log(err));
    },
    updatedPreferences() {
      const update = this.preferences;
      this.saving = true;

      api
        .updateOne(`/users/${this.user.id}/notification-settings`, update)
        .then(result => {
          this.preferences = result;
          this.saving = false;
          this.saved = true;
          setTimeout(() => (this.saved = false), 3000);
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../../scss/vars.scss";

.account {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: min-content 1fr;
  grid-template-areas:
    "header header"
    "nav content";
  grid-gap: spacing(2);
  min-height: 0;
  min-width: 0;
  height: 100%;
}

.account--header {
  grid-area: header;
}

.account--nav {
  grid-area: nav;
  overflow-y: auto;
}

.account--prefs {
  grid-area: content;
  overflow-y: auto;
  min-height: 0;
  min-width: 0;
  padding-bottom: spacing(10);
}

.prefs--section {
  margin-bottom: spacing(5);
}

.toggle--group {
  max-width: 600px;
}

.account--nav-item {
  @include toggleable-button;
  display: block;
}
</style>

 


