<template>
  <div class="checklist--item" v-bind:class="[localTodo.state === 'COMPLETE' ? 's-complete' : 's-incomplete']">
        <div class="checklist--check" @click="toggleItemStatus">
          <i class="icon-check-mark"></i>
        </div>
        <input type="text" class="checklist--edit" v-if="editing" v-model="localTodo.name" placeholder="Todo Title"/>
        <button class="checklist--save" v-if="editing" @click="saveChanges">Save</button>
        <div class="checklist--value" v-if="!editing" @dblclick="toggleEditing">{{ localTodo.name }}</div>
        <div class="drag-handle"></div>
    </div>
</template>

<script>
import api from "./../api.js";

export default {
  name: "ChecklistItem",
  props: ["todo", "url"],
  data: function() {
    return {
      editing: false,
      localTodo: this.todo
    };
  },
  methods: {
    toggleEditing() {
      this.editing = true;
    },
    saveChanges() {
      this.editing = false;

      const update = {
        name: this.localTodo.name
      };

      api
        .updateOne(`${this.url}${this.localTodo.id}`, update)
        .then(result => console.log("success", result))
        .catch(err => console.log(err));
    },
    toggleItemStatus() {
      const newstate =
        this.localTodo.state === "INCOMPLETE" ? "COMPLETE" : "INCOMPLETE";
      const update = {
        state: newstate
      };

      api
        .updateOne(`${this.url}${this.localTodo.id}`, update)
        .then(result => {
          console.log("success", result);
          this.localTodo = result;
        })
        .catch(err => console.log(err));
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.checklist--item {
  display: grid;
  grid-template-columns: 20px 1fr auto 20px;
  grid-template-areas: "check value button handle";
  padding-bottom: spacing(1);
}

.checklist--check {
  grid-area: check;
  display: block;
  width: 20px;
  height: 20px;
  border-radius: spacing(0.5);
  border: 2px solid palette(grey, 2);
  user-select: none;
  display: grid;

  i {
    visibility: hidden;
    color: white;
    font-size: 10px;
    align-self: center;
    justify-self: center;
  }

  .s-complete & {
    background-color: palette(green);
    border-color: palette(green);

    i {
      visibility: visible;
    }
  }
}

.checklist--value {
  grid-column: 2 / span 2;
  padding-left: spacing(2);

  .s-complete & {
    text-decoration: line-through;
  }
}

.checklist--edit {
  grid-area: value;
  margin: 0 spacing(2);
  font-size: fontsize(100);
  padding: spacing(0.5) spacing(1);
}

.checklist--save {
  grid-area: button;
}

.drag-handle {
  grid-area: handle;
  display: block;
  width: 30px;
  cursor: grab;
}
</style>


