<template>
    <div>
      
      <div v-if="user && user.user" class="user--avatar">
       <div class="user--avatar__photo" v-if="user.user.imageUrl"><img v-bind:src="`${baseURL}${user.user.imageUrl}`"/></div>
       <div class="user--avatar__non-human--initials" v-else>{{ userNameInitials(user.user.fullName) }}</div>
       <div class="user--details" v-if="viewMode === 'DETAILED'">
        <h3>{{ user.user.fullName }}</h3>
        <div class="user--label">{{ user.userAccess }}</div>
       </div>
      </div>

      <div v-else-if="user" class="user--avatar">
        <div class="user--avatar__non-human--placeholder">{{ userNameInitials(user.name) }}</div>
        <div class="user--details" v-if="viewMode === 'DETAILED'">
          <h3>{{user.name}}</h3>
          <div class="user--label">Placeholder</div>
        </div>
      </div>

      <div v-else class="user--avatar">
        <div class="user--avatar__non-human--ghost"><i class="icon-ghost"></i></div>
      </div>
    </div>
</template>

<script>
import { BASE_AVATAR_URL } from "./../api.js";

export default {
  name: "User",
  props: ["view-mode", "user"],
  data() {
    return {
      baseURL: BASE_AVATAR_URL
    };
  },
  methods: {
    userNameInitials(fullName) {
      const names = fullName.split(" ");
      const firstInitial = names[0].charAt(0);
      const secondInitial = names.length > 1 ? names[1].charAt(0) : "";
      return firstInitial + secondInitial;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./../scss/vars";

.user--avatar {
  display: grid;
  grid-template-columns: 30px 1fr min-content;
  grid-template-areas: "avatar label actions";
  grid-gap: spacing(4);
  margin-bottom: spacing(2);
}

.user--details {
  grid-area: label;
  align-content: center;
}

.user--label {
  @include label-small;
}

*[class^="user--avatar__"] {
  grid-area: avatar;
  width: spacing(5);
  height: spacing(5);
  border-radius: 50%;
  overflow: hidden;
  user-select: none;

  img {
    object-fit: cover;
    width: spacing(5);
    height: spacing(5);
  }
}

*[class^="user--avatar__non-human"] {
  background: linear-gradient(135deg, palette(blue), palette(green, 2));
  color: white;
  font-weight: 700;
  font-size: 20px;
  display: grid;
  justify-content: center;
  align-content: center;
}

.user--avatar__non-human--placeholder {
  background: linear-gradient(135deg, palette(brand), palette(red, 1));
}

.user--avatar__non-human--ghost {
  background: linear-gradient(135deg, palette(grey, 2), palette(grey, 1));
  opacity: 0.5;
}
</style>



